import * as Yup from 'yup';
import { Servico } from '../../../../models/Movimentos/Servico';


export const ServicoSchema = Yup.object().shape<Servico>({
    id: Yup.number(),

    nome: Yup.string()
        .max(60, "Servio no deve possuir mais de 60 caracteres.")
        .required('Servio no pode ser vaziu.'),

    valor: Yup.number()
        .min(-0.00001, "O valor no pode ser negativo.")
        .required("Informe o valor."),

    categoriaId: Yup.number()
        .required("Informe a Categoria.")
        .min(0, "Informe a Categoria."),

    descricao: Yup.string()
        .nullable()
        .max(255, "Descrio no deve possuir mais de 255 caracteres."),

    observacao: Yup.string()
        .nullable()
        .max(255, "Observao no deve possuir mais de 255 caracteres."),
});
