import * as Yup from 'yup';
import { UnidadeMedida } from '../../../../models/Movimentos/UnidadeMedida';


export const UnidadeMedidaSchema = Yup.object().shape<UnidadeMedida>({
    id: Yup.string()
        .max(3, "O campo Cdigo no deve possuir mais de 3 caracteres.")
        .min(1, "O campo Cdigo deve possuir mais de 1 caracter.")
        .required('O campo Cdigo no pode ser vaziu.'),

    nome: Yup.string()
        .max(50, "O campo Nome no deve possuir mais de 50 caracteres.")
        .required('Nome da Unidade Medida no pode ser vaziu.')
});