import * as Yup from 'yup';
import { Cliente } from '../../../../models/Pessoas/Cliente';
import { NATIONALITY_TYPE } from '../../../../components/NationalitySelect/NationalitySelect';
import { TIPO_PESSOA } from '../../../../models/Pessoas/Pessoa';
// const regexCPFV1 = /(^\d{3}\.\d{3}\.\d{3}\-\d{2}$)|(^\d{2}\.\d{3}\.\d{3}\/\d{4}\-\d{2}$)/;
const regexCPF = /([0-9]{2}[.]?[0-9]{3}[.]?[0-9]{3}[/]?[0-9]{4}[-]?[0-9]{2})|([0-9]{3}[.]?[0-9]{3}[.]?[0-9]{3}[-]?[0-9]{2})/;
const regexRG = /((^[A-Z]{2}-?)?([0-9\\.-]{5,}))([A-Z]{3})?([A-Z]{2})?/

export const ClienteSchema = Yup.object().shape<Cliente>({
    nome: Yup.string()
        .nullable()
        .max(60, "O campo no deve possuir mais de 60 caracteres.")
        .min(5, "O campo deve possuir mais de 5 caracteres.")
        .required('Informe o Cliente.'),

    apelido: Yup.string()
        .nullable()
        .max(60, "O campo no deve possuir mais de 60 caracteres."),

    bairro: Yup.string()
        .nullable()
        .max(60, "O Bairro no deve possuir mais de 60 caracteres.")
        .required('Informe Bairro.'),

    cep: Yup.string()
        .nullable()
        .max(9, "O CEP no deve possuir mais de 9 caracteres.")
        .min(8, "O CEP deve possuir mais de 8 caracteres.")
        .required('Informe o CEP.'),

    complemento: Yup.string()
        .nullable()
        .max(60, "O Complemento no deve possuir mais de 60 caracteres"),

    observacao: Yup.string()
        .nullable()
        .max(255, "A Descrio no deve possuir mais de 255 caracteres"),

    cpfcpnj: Yup.string().when("nacionalidade", (nacionalidade: NATIONALITY_TYPE, schema: any) => {
        if (nacionalidade === NATIONALITY_TYPE.BRASILEIRO) {
            return Yup.string()
                .nullable()
                .required("Informe o CPF/CNPJ.")
                .max(16, "O CPF/CNPJ no deve possuir mais de 16 caracteres")
                .min(5, "O CPF/CNPJ deve possuir mais de 5 caracteres")
                .matches(regexCPF, "CPF/CNPJ no  vlido.")
        }
    }),

    dataNascimento: Yup.date()
        .nullable()
        .typeError("Informe a data.")
        .required("Informe a data.")
        .max(new Date(), "Data invlida."),

    email: Yup.string()
        .nullable()
        .required("Informe o Email.")
        .max(60, "O Email no deve possuir mais de 60 caracteres")
        .email("Informe um email vlido"),

    endereco: Yup.string()
        .nullable()
        .min(5, "O Endereo deve possuir mais de 5 caracteres.")
        .max(60, "O Endereo no deve possuir mais de 60 caracteres.")
        .required('Informe o Endereo.'),

    estadoCivil: Yup.mixed()
        .when("tipo", (tipo: TIPO_PESSOA, schema: any) => {
            if (tipo === TIPO_PESSOA.Juridica) {
                return Yup.string().nullable();
            }

            return Yup.string().nullable().required('Informe o Estado Civl.')
        }),

    limiteCredito: Yup.number()
        .nullable()
        .typeError("Informe um valor vlido")
        .required('Informe o Limite de credito.')
        .min(-0.00001, "O Limite de credito no pode ser negativo"),

    nacionalidade: Yup.string()
        .when("tipo", (tipo: TIPO_PESSOA, schema: any) => {
            if (tipo === TIPO_PESSOA.Juridica) {
                return Yup.string().nullable();
            }

            return Yup.string()
                .nullable()
                .required('Informe a Nacionalidade.')
                .min(5, "A Nacionalidade deve possuir mais de 5 caracteres.")
                .max(60, "A Nacionalidade no deve possuir mais de 60 caracteres.")
        }),

    rgInscricaoEstadual: Yup.string()
        .nullable()
        .matches(regexRG, "Documento invlido.")
        .max(14, "Documento invlido.")
        .required("Informe o Documento."),

    sexo: Yup.mixed()
        .when("tipo", (tipo: TIPO_PESSOA, schema: any) => {
            if (tipo === TIPO_PESSOA.Juridica) {
                return Yup.string();
            }

            return Yup.string().required("Informe o sexo.").nullable()
        }),

    telefone: Yup.string()
        .nullable()
        .min(5, "Informe um telefone vlido,")
        .max(30, "O Telefone no pode ter mais de 30 caracteres.")
        .required("Informe um telefone."),

    cidadeId: Yup.number()
        .nullable().required("Informe a Cidade."),

    numero: Yup.string()
        .nullable()
        .typeError("Informe o Nmero.")
        .min(1, "O Nmero deve possuir mais de 1 caracteres.")
        .max(10, "O Nmero no pode ter mais de 10 caracteres.")
        .required("Informe o Nmero."),

    condicaoPagamentoId: Yup.number().required("Informe a Condio de pagemento."),

});