import React from 'react';
import FormBasicLayout from '../../../../layouts/FormBasicLayout/FormBasicLayout';
import { RouteComponentProps } from 'react-router-dom';
import ListForm from '../../../../components/ListForm/ListForm';
import { UseListPagined } from '../../../../hoc/UseListPagined';
import { UserApi } from '../../../../apis/Pessoas/UserApi';

const ListUser: React.FC<RouteComponentProps> = () => {

    const response = UseListPagined({ getListPagined: UserApi.GetListPagined.bind(UserApi) });

    const columns = [
        {
            title: 'Cdigo',
            dataIndex: 'id',
            key: 'id',
        },
        {
            title: 'Usurio',
            dataIndex: 'userName',
            key: 'userName',
        },
        {
            title: 'Email',
            dataIndex: 'email',
            key: 'email',
        },
        {
            title: 'Telefone',
            dataIndex: 'phoneNumber',
            key: 'phoneNumber',
        },

    ];


    return (
        <FormBasicLayout breadcrumbList={[{ displayName: "Usurios", URL: "/User" }, { displayName: "Listagem", URL: undefined }]} >


            <ListForm
                tableProps={response}
                columns={columns} />


        </FormBasicLayout>
    );

}

export default ListUser;
