import * as Yup from 'yup';
import { Produto } from '../../../../models/Movimentos/Produto';


export const ProdutoSchema = Yup.object().shape<Produto>({
    id: Yup.number(),

    nome: Yup.string()
        .nullable()
        .max(60, "Produto no deve possuir mais de 60 caracteres.")
        .min(5, "Produto deve possuir mais de 5 caracteres.")
        .required('Produto no pode ser vaziu.'),

    marcaId: Yup.number()
        .nullable()
        .required("Informe a Marca.")
        .min(0, "Informe a Marca."),

    quantidade: Yup.number()
        .nullable()
        .required("Informe a Quantidade.")
        .min(0, "Informe a Quantidade."),

    quantidadeMinima: Yup.number()
        .nullable()
        .required("Informe a Quantidade Mnima.")
        .min(0, "Informe a Quantidade Mnima."),

    valorCompra: Yup.number()
        .nullable()
        .required("Informe o Valor de Compra.")
        .min(0, "Informe o Valor de Compra."),

    valorVenda: Yup.number()
        .nullable()
        .required("Informe o Valor de Venda.")
        .min(0, "Informe o Valor de Venda."),

    categoriaId: Yup.number()
        .nullable()
        .required("Informe a Categoria.")
        .min(0, "Informe a Categoria."),

    unidadeMedidaId: Yup.string()
        .nullable()
        .required("Informe uma Unidade de Medida."),

    codigoBarras: Yup.string()
        .nullable()
        .max(20, "Codigo de Barras no deve possuir mais de 20 caracteres."),

    referencia: Yup.string()
        .nullable()
        .max(60, "Referncia no deve possuir mais de 60 caracteres.")
        .min(5, "Referncia deve possuir mais de 5 caracteres.")
        .required('Referncia no pode ser vazia.'),

    descricao: Yup.string()
        .nullable()
        .max(255, "Descrio no deve possuir mais de 255 caracteres."),

    observacao: Yup.string()
        .nullable()
        .max(255, "Observao no deve possuir mais de 255 caracteres."),
});
