import * as Yup from 'yup';
import { FuncaoFuncionario } from '../../../../models/Pessoas/FuncaoFuncionario';


export const FuncaoFuncionarioSchema = Yup.object().shape<FuncaoFuncionario>({

    nome: Yup.string()
        .nullable()
        .max(50, "O campo [Nome] no deve possuir mais de 50 caracteres.")
        .required('[Nome] da Funo do Funcionrio no pode ser vaziu.'),

    cargaHoraria: Yup.number()
        .nullable()
        .min(0.0001, "A Carga Horria no pode ser igual ou inferior a 0.")
        .required('A Carga Horria deve ser informada.'),

    observacao: Yup.string().nullable(),
    
    descricao: Yup.string().nullable(),
});
